.preloader  {
  position: fixed;
  top: 0;  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  overflow: hidden;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  &__loader {
    svg {
      max-width: 200px;
      path{
        fill: #fff;
      }
      .change-fill {
        transition: fill .3s ease;
        animation: 2s linear infinite changefill;
      }
    }
  }
  &.hide {
    opacity: 0;
    transition: 1s;
    transition-delay: .2s;
    pointer-events: none;
  }
  &.remove {
    display: none;
  }

}
.start {
  position: fixed;
  top: 0;  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
  overflow: hidden;
  background-color: #fff;
  transition: transform 1s ease 3s, opacity 1s ease 4.5s;
  @media (max-width: 1025px) {
    display: none;
  }
  &:before{
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #000;
    transition: transform 1s ease 2.5s;
  }
  &__imgs {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    opacity: 0;
    transition: opacity .2s linear;
  }
  &__img {
    position: absolute;
    width: auto;
    height: 100%;
    transition: transform .5s linear 1s;
    @media (max-width: 1025px) {
      display: none;
    }
    &_1 {
      left: 13.5%;
      top: -14%;
      height: auto;
      max-width: 345px;
      transform: translate(-55px, -97px);
      @media (max-width: 1800px) {
        left: 11.5%;
        top: -38%;
      }
      @media (max-width: 1280px) {
        top: -62%;
        left: 11.5%;

      }
    }
    &_2 {
      left: 38%;
      top: 9%;
      max-width: 352px;
      height: auto;
      transform: translate(-5px, 78px);
      @media (max-width: 1800px) {
        left: 38%;
        top: 9%;
      }
     @media (max-width: 1280px) {
        top: 2%;
        left: 38%;
      }
    }
    &_3 {
      left: 0%;
      top: 44%;
      max-width: 682px;
      height: auto;
      transform: translate(-100px, 94px);
      @media (max-width: 1800px) {
        left: -13.5%;
        top: 44%;
      }
       @media (max-width: 1280px) {
        top: 41%;
        left: -13.5%;

      }
    }
    &_4 {
      right: 4%;
      top: -6%;
      max-width: 796px;
      height: auto;
      transform: translate(81px, -97px);
       @media (max-width: 1800px) {
        right: -14%;
        top: -32%;
      }
      @media (max-width: 1280px) {
        top: -48%;
        right: -18%;
      }
    }
    &_5 {
      right: 7%;
      top: 51%;
      max-width: 571px;
      height: auto;
      transform: translate(4px, 105px);
       @media (max-width: 1800px) {
        right: -4%;
        top: 44%;
      }
    }
  }
}


body{
  &.show {
    .start {
      transform: translateY(-100%);
      opacity: 0;
      &:before {
        transform: translateY(100%);
      }
      &__imgs {
        opacity: 1;
      }
      &__img{
        transform: translate(0, 0);
      }
      &__imgs {
        transition: transform 2s ease 2.2s;
        transform: rotate(-17deg);
      }
    }
  }
  .header {
    opacity: 1;
  }
  @media (min-width: 1025px) {
    .hero {
        animation: up-anim 2s ease 2.5s;
    }
    .hero .container h1 {
      animation: up-anim 2s ease 3s;
    }
    .hero canvas{
      animation: up-anim 2s ease 3s;
    }
      .hero .hero__desc {
      animation: up-anim 2s ease 3.2s;
    }
  }
}


@keyframes changefill {
  0% {
    fill: #0000FF;
  }
  50% {
    fill: #fff;
  }
  100% {
    fill: #0000FF;
  }
}


@keyframes up-anim {
  0%
{    opacity: 0;
    transform: translateY(50%);}
  40%
{    opacity: 0;
    transform: translateY(50%);}
  70%
{    opacity: 1;
    transform: none;}
  100%
{    opacity: 1;
    transform: none;}
}
